import React, { useState } from 'react';

import './index.scss';

interface ButtonProps {
  text: string;
}

export const Button = ({ text }: ButtonProps) => {
  const [count, setCount] = useState(0);
  const handleClick = () => {
    const add = count + 1;
    setCount(add);
  };
  return (
    <div className="nui__btn" onClick={handleClick}>
      {text} - {count}
    </div>
  );
};

export default Button;
try {
    // @ts-ignore
    Button.displayName = "Button";
    // @ts-ignore
    Button.__docgenInfo = { "description": "", "displayName": "Button", "props": { "text": { "defaultValue": null, "description": "", "name": "text", "required": true, "type": { "name": "string" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/Button/index.tsx#Button"] = { docgenInfo: Button.__docgenInfo, name: "Button", path: "src/components/Button/index.tsx#Button" };
}
